import React from 'react';
import "./JoinUs.css";

const JoinUs = () => {
    return(
        <section>
            <div className="banner2">
                <p className="heroText">Work with us</p>
            </div>
            <div className="parallax imgJoin"/>
            <div className="joinUsBox">
                <div className="joinBoxLeft">
                    <p>WORK AT LEGIS CHAMBER</p>
                    <p>
                        We are currently recruiting 
                        trainees for summer internship
                        from different law schools in 
                        India and UK. Successful 
                        candidates will be appointed 
                        and offered internship and 
                        also will be paid basic 
                        remuneration based on their 
                        skills and experience, 
                        including bonus depending on 
                        their work.
                    </p>
                </div>
                <div>
                    <form className="formStyle">
                        <input className="formInput" type="text" placeholder="Name"/>
                        <input className="formInput" type="text" placeholder="Phone"/>
                        <input className="formInput" type="email" placeholder="Email"/>
                        <input className="formInput" type="textarea" placeholder="About Yourself"/>
                        <button className="formBtn" type="submit">send</button>
                    </form>
                </div>
            </div>
        </section>
    )
}

export default JoinUs;